<template>
    <ul class="pager">
        <!--        -->
        <li class="number"
            :class="{active: this.current===1}"
            @click="setPage(1)">1</li>

        <!--        -->
        <li class="more left"
            v-if="totalPage>centerSize+2 && current-centerSize/2-1>1"
            @click="setPage(current-jumpSize)">...</li>

        <!--        -->
        <li class="number"
            v-for="(page,index) in centerPages"
            :class="{active: current===page}"
            :key="index"
            @click="setPage(page)">{{ page }}</li>

        <!--        -->
        <li class="more right"
            v-if="totalPage>centerSize+2 && current+centerSize/2+1<totalPage"
            @click="setPage(current+jumpSize)">...</li>

        <!--        -->
        <li class="number"
            v-if="totalPage!==1"
            :class="{active: this.current=== totalPage}"
            @click="setPage(totalPage)">{{ totalPage }}</li>
    </ul>
</template>

<script>
  export default {
    name: "Pager",
    props:{
      totalPage:Number,//
      defaultCurrentPage:Number,//
      //5
      centerSize:{
        type:Number,
        default(){
          return 5;
        }
      },
      jumpSize:{
        type:Number,
        default(){
          return 5;
        }
      }
    },
    computed:{
      // 
      centerPages(){
        let centerPage=this.current;
        // this.current+2(current) +1()>this.totalPagethis.totalPage-3
        if(this.current>this.totalPage-3){
          centerPage=this.totalPage-3;//centerPagethis.current
        }
        // 44
        if(this.current<4){
          centerPage=4;
        }

        if(this.totalPage<=this.centerSize+2){
          // 
          const centerArr=[];
          for(let i=2;i<this.totalPage;i++){
            centerArr.push(i);
          }
          return centerArr;
        }
        else{
          // centerSize
          const centerArr=[];
          for(let i=centerPage-2;i<=centerPage+2;i++){
            centerArr.push(i);
          }
          return centerArr;
        }
      }
    },

    data(){
      return{
        // propsdatacurrentpropsdefaultCurrentPagewatchpropsdefaultCurrentPage
        // datacurrentwatchbug
        current:this.defaultCurrentPage,
      }
    },

    watch:{
      // propsdefaultCurrentPagecurrent
      defaultCurrentPage:function(newValue,oldValue){
        this.current=newValue;
      }
    },

    methods:{
      // 
      setPage(page){
        // 
        if(page<1) this.current=1;
        // 
        else if(page>this.totalPage){
          this.current=this.totalPage;
        }
        // 
        else{
          this.current=page;
        }
        // pager
        this.$emit('change',this.current);
      },
    }
  }
</script>

<style scoped>
    ul{
        list-style: none;
        height: 30px;
        padding-left: 8px;
    }
 ul li{
      font-size: 14px;
     float: left;
     width:30px;
     height:30px;
     line-height: 30px;
     margin:0 8px 0 0;
     padding:0 6px;
     text-align: center;
     /* border:1px solid #d9d9d9; */
     border-radius:2px;
     transition: all 0.3s;
     user-select: none;
 }
    .active{
       background: #f1f3f6;
    border: none;
    box-shadow: 2px 2px 4px #c6c7ca, -3px -3px 6px #ffffff;
    }

    .more left:hover, .more right:hover{
        background: #7d3990;
    }
</style>
